.section--partners {
	margin-bottom: 150px;

	@include r(767) {
		margin-bottom: 50px; }

	.section__title {
		span:last-child {
			@include r(767) {
				display: block; } } } }

.partners__list {
	display: flex;
	flex-flow: row nowrap;
	justify-content: flex-start;
	align-items: center;

	@include r(1400) {
		flex-flow: row nowrap; }

	@include r(1100) {
		flex-wrap: wrap; } }

.partners__item {
	flex: 1 1;
	max-width: 280px;
	margin: 15px 30px;

	@include r(1400) {
		flex-basis: 50%; }

	@include r(1100) {
		flex-basis: auto;
		width: 100%; } }
