@import url("https://cdnjs.cloudflare.com/ajax/libs/tiny-slider/2.9.2/tiny-slider.css");

// Courses section
.section--courses {
	margin-bottom: 150px;

	@include r(767) {
		margin-bottom: 50px; }

	// Sliders configs
	.tns-controls, .tns-nav, button[data-action=stop], button[data-action=start] {
		display: none; }

	.tns-outer {
		@include r(767) {
			position: relative; } }

	.tns-controls {
		@include r(767) {
			display: block; }

		> button {
			position: absolute;
			z-index: 2;
			top: 50%;
			transform: translateY(-50%);
			left: -10px;
			width: 40px;
			height: 40px;

			&:last-child {
				left: auto;
				right: -10px;

				&::before {
					left: 44%;
					transform: translate(-50%, -50%) rotate(-135deg); } }

			&::before {
				content: '';
				position: absolute;
				top: 50%;
				left: 58%;
				transform: translate(-50%, -50%) rotate(45deg);
				width: 15px;
				height: 15px;
				border-bottom: 2px solid $dark;
				border-left: 2px solid $dark; }

			&:focus {
				&::before {
					border-color: $white; } } } } }

.courses {
	.header__contact {
		@include r(767) {
			display: block; }

		&::before {
			max-width: 165px;
			height: 44px;

			@include r(767) {
				max-width: 100%; } }

		a {
			max-width: 165px;
			height: 44px;
			line-height: 44px;

			@include r(767) {
				max-width: 100%; } } } }

.courses__sliderLabel {
	width: 70px;
	height: 70px;
	padding: 10px;
	display: flex;
	justify-content: flex-end;
	align-items: flex-end;
	margin-left: 80px;
	color: $white;
	font-size: 14px;
	font-weight: 600;
	line-height: 1.15;
	background-color: $dark;

	@include r(767) {
		margin-left: 0; } }

.courses__sliderWrapper {
	margin-bottom: 30px; }

.courses__slider {
	img {
		display: block;
		width: 100%;
		height: auto; } }
